.search {
  background: linear-gradient(180deg, #FFF 0%, #EFF2F7 99.96%);
  box-shadow: -10px -10px 20px 0px #FFF, 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25);
  padding-bottom: 100px;
  position: relative;
  z-index: 2;

  &__content {
    margin-top: 40px;
  }

  &__title {
    margin-bottom: 48px;
  }

  &__block {
    border-radius: 20px;
    background-color: $white;
    box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
    padding: 32px 60px;

    &-name {
      @extend %regular16;
      color: $grey;
      margin-bottom: 16px;
    }

    &-results {
      @extend %regular20;
      margin-top: 34px;
    }
  }

  &__nothing {
    @extend %regular24;
    margin-top: 48px;
  }
}

.form-search {
  @include flexCBetween;
  column-gap: 24px;

  .form__label {
    width: 100%;
    display: inline-block;
  }

  .form__input {
    width: 100%;
    padding: 12px 16px;
    border-radius: 4px;
    border: 1px solid $border;
    background-color: $white;
    @extend %regular16;
    height: 48px;
  }
}

.btn {
  padding: 14px 32px;
  border-radius: 55px;
  background: var(--5, linear-gradient(90deg, #FFC700 1.97%, #FBE38E 96.94%));
  box-shadow: 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
  @extend %bold14;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}

@media screen and (max-width: $laptop) {
  .search {
    padding-bottom: 32px;

    &__content {
      margin-top: 16px;
    }

    &__title {
      margin-bottom: 16px;
    }

    .form {
      &__label {
        @include mb(0px);
      }
    }

    &__nothing {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: $mob) {
  .search {

    &__block {
      padding: 16px 24px;

      &-name {
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 4px;
      }

      &-results {
        margin-top: 16px;
      }
    }

    .form {
      flex-direction: column;
      row-gap: 22px;

      &__label {
        @include mb(0px);
      }

      &__btn {
        font-size: 14px;
        line-height: 20px;
        width: 100%;
      }
    }

    &__nothing {
      font-size: 20px;
      line-height: 28px;
    }
  }
}

.section-results {
  background-color: $blue;

  &__block {
    border-radius: 20px;
    background-color: $white;
    box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
    padding: 64px 60px;
  }

  &__items {
    @include flexColumn;
    row-gap: 24px;
  }

  &__item {
    @include flexFsStart;
    padding-bottom: 24px;
    border-bottom: 1px solid $border-line;
    column-gap: 30px;

    &-num {
      @extend %regular16;
      width: 23px;
      flex-shrink: 0;
      @include flexCenter;
    }

    &-image {
      width: 120px;
      height: 120px;
      flex-shrink: 0;
      border-radius: 20px;
      overflow: hidden;
      display: inline-block;

      img {
        width: 100%;
        height: 100%;
      }
    }

    &-right {
      display: contents;
    }

    &-content {
      margin-bottom: auto;
    }

    &-name {
      display: inline-block;
      color: $green;
      margin-bottom: 12px;
    }

    &-text {
      @extend %regular16;

      p {
        color: $grey;
        @include mb(8px);
      }
    }
  }
}

@media screen and (max-width: $laptop) {
  .section-results {
    &__block {
      padding: 64px 20px;
    }

    &__item {
      &-name {
        font-size: 20px;
        line-height: 28px;
      }
    }
  }
}

@media screen and (max-width: $mob) {
  .section-results {
    &__block {
      padding: 16px;
    }

    &__item {
      column-gap: 16px;
      padding-bottom: 16px;

      &-image {
        width: 64px;
        height: 64px;
      }

      &-right {
        display: flex;
        flex-direction: column;
        row-gap: 16px;
      }

      &-name {
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 8px;
      }

    }
  }
}