.offset-preview {
  &__item {
    padding-top: 83px;
    padding-bottom: 80px;
    min-height: 600px;

    &::before {
      display: none;
    }
  }

  &__logo {
    width: 238px;
    height: 160px;
    flex-shrink: 0;
    margin-bottom: 16px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    margin-bottom: 24px;
  }

  &__text {
    max-width: 597px;
    margin-bottom: 40px;
  }

  &__info {
    column-gap: 64px;
  }

  &__link {
    padding: 14px 70px 14px 32px;
  }
}

@media screen and (max-width: $laptop) {
  .offset-preview {
    &__item {
      padding-top: 63px;
      padding-bottom: 32px;
      min-height: auto;
    }

    &__logo {
      width: 95px;
      height: 64px;
    }

    &__title {
      margin-bottom: 16px;
    }

    &__text {
      margin-bottom: 24px;
    }

    &__info {
      column-gap: 24px;
    }
  }
}

@media screen and (max-width: $mob) {
  .offset-preview {
    &__item {
      padding-top: 64px;
    }

    &__logo {
      width: 120px;
      height: 80px;
    }

    &__title {
      margin-bottom: 8px;
    }

    &__text {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 16px;
    }

    &__info {
      flex-direction: column;
      row-gap: 16px;
    }

    &__link {
      width: 100%;
    }
  }
}

.offset-tech {
  background: linear-gradient(180deg, #FFF 0%, #EFF2F7 10.93%);

  &__container {
    @include flexColumn;
    row-gap: 64px;
  }

  &__item {
    display: grid;
    grid-template-columns: 5fr 7fr;
    align-items: center;

    &:nth-child(even) {
      grid-template-columns: 7fr 5fr;

      .offset-tech__item-image {
        order: 2;
      }

      .offset-tech__item-content {
        order: 1;
        padding-left: 0;
        padding-right: 60px;
      }
    }

    &-image {
      @include imageCoverWidth;
      height: 400px;
      flex-shrink: 0;
      overflow: hidden;
      border-radius: 20px;
      box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
    }

    &-content {
      padding-left: 60px;
    }

    &-name {
      color: $green;
      margin-bottom: 16px;
    }
  }
}

@media screen and (max-width: $laptop) {
  .offset-tech {
    &__item {
      &:nth-child(even) {
        .offset-tech__item-content {
          padding-right: 40px;
        }
      }

      &-image {
        height: 360px;
      }

      &-content {
        padding-left: 40px;
      }
    }
  }
}

@media screen and (max-width: $tablet) {
  .offset-tech {
    &__item {
      grid-template-columns: 1fr;
      row-gap: 40px;

      &:nth-child(even) {
        grid-template-columns: 1fr;

        .offset-tech__item-image {
          order: 1;
        }

        .offset-tech__item-content {
          order: 2;
          padding-left: 0;
          padding-right: 0px;
        }
      }

      &-image {
        width: 522px;
      }

      &-content {
        padding-left: 0px;
      }
    }
  }
}

@media screen and (max-width: $mob) {
  .offset-tech {

    &__container {
      row-gap: 40px;
    }

    &__item {
      row-gap: 16px;

      &-image {
        width: 100%;
        height: auto;
      }

      &-name {
        margin-bottom: 8px;
      }

      &-text {
        p {
          &:not(:last-child) {
            margin-bottom: 8px;
          }

          &:not(:first-child) {
            margin-top: 8px;
          }
        }

        ul {
          li {
            @include mb(8px);

            &:before {
              top: 8px;
            }
          }
        }
      }
    }
  }
}

.offset-use {
  box-shadow: 0px 0px 30px 0px rgba(170, 170, 204, 0.50) inset, 0px 5px 20px 0px rgba(170, 170, 204, 0.25) inset, 0px 5px 10px 0px rgba(170, 170, 204, 0.25) inset;
