    &__title {
      margin-bottom: 8px;
    }

    &__item {
      height: 200px;
    }

    &__text {
      margin-bottom: 16px;

      p {
        &:not(:last-child) {
          margin-bottom: 8px;
        }

        &:not(:first-child) {
          margin-top: 8px;
        }
      }
    }
  }
}

.offset-advan {
  background: linear-gradient(180deg, #FFF 0%, #EFF2F7 99.96%);
  box-shadow: -10px -10px 20px 0px #FFF, 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25);

  &__title {
    margin-bottom: 40px;
  }

  &__items {
    @include gridColumn(4);
    column-gap: 40px;
  }

  &__item {
    text-align: center;

    &-image {
      @include flexCCenter;
      border-radius: 666px;
      background-color: $greyCircle;
      box-shadow: -10px -10px 20px 0px #FFF inset, 10px 10px 20px 0px rgba(170, 170, 204, 0.50) inset, 5px 5px 10px 0px rgba(170, 170, 204, 0.25) inset, -5px -5px 10px 0px rgba(255, 255, 255, 0.50) inset;
      width: 100px;
      height: 100px;
      flex-shrink: 0;
      margin: 0 auto;
      margin-bottom: 24px;

      &-inner {
        width: 64px;
        height: 64px;
        flex-shrink: 0;

        img {
          width: 100%;
          height: 100%;
        }
      }
    }

    &-name {
      color: $green;
      margin-bottom: 8px;
    }


    &-text {
      @extend %regular20;
    }
  }
}

@media screen and (max-width: $laptop) {
  .offset-advan {
    &__items {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 40px;
    }
  }
}

@media screen and (max-width: $mob) {
  .offset-advan {
    &__title {
      margin-bottom: 24px;
    }

    &__items {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 24px;
    }

    &__item {
      &-image {
        margin-bottom: 16px;
      }
    }
  }
}

.offset-info {
  background-color: $blue;

  &__container {
    @include flexColumn;
    row-gap: 100px;
  }
}

@media screen and (max-width: $laptop) {
  .offset-info {
    &__container {
      row-gap: 64px;
    }
  }
}

@media screen and (max-width: $mob) {
  .offset-info {
    &__container {
      row-gap: 24px;
    }
  }
}

.offset-table {
  &__title {
    margin-bottom: 40px;
  }

  &__table {}

  &__specification {
    display: none;
  }
}

table {
  border-radius: 20px;
  background: #FFF;
  box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
  width: 100%;
  overflow: hidden;

  thead {
    background: var(--Linear, linear-gradient(90deg, #004D43 1.97%, #02B45E 96.94%));
    text-align: center;

    tr {
      &:not(:last-child) {
        border-bottom: 1px solid rgba(255, 255, 255, 0.10);
      }

      &:first-child {

        th,
        td:nth-child(2) {
          text-align: left;
        }
      }

      th,
      td {
        @extend %bold16;
        color: $white;
        vertical-align: middle;
        padding: 8px 24px;
      }

      th {
        border-right: 1px solid rgba(255, 255, 255, 0.10);
      }

      td {

        &:not(:last-child) {
          border-right: 1px solid rgba(255, 255, 255, 0.10);
        }
      }
    }
  }
}

tbody {
  tr {
    background-color: $white;

    &:not(:last-child) {
      border-bottom: 1px solid $greyLine;
    }

    &:nth-child(even) {
      background-color: $table-line;
    }

    th,
    td {
      vertical-align: middle;
    }

    th {
      padding: 8px 24px;
      @extend %bold16;
      text-align: left;
      border-right: 1px solid $greyLine;
      width: 300px;
      flex-shrink: 0;
    }

    td {
      padding: 20px 24px;
      @extend %regular16;
      text-align: center;

      &:nth-child(2) {
        width: 184px;
        flex-shrink: 0;
        text-align: left;
      }

      &:not(:last-child) {
        border-right: 1px solid $greyLine;
      }
    }
  }
}

@media screen and (max-width: $laptop) {
  .offset-table {
    &__title {
      margin-bottom: 24px;
    }

    &__table {
      overflow-x: auto;
      padding-bottom: 24px;

      &::-webkit-scrollbar {
        background-color: $greyLine;
        border-radius: 444px;
      }

      &::-webkit-scrollbar-thumb {
        width: 88px;
        height: 10px;
        border-radius: 444px;
        background: var(--Linear, linear-gradient(90deg, #004D43 1.97%, #02B45E 96.94%));
      }


    }
  }

  table {
    tbody {
      tr {
        th {
          width: 280px;
        }

        td {
          &:nth-child(2) {
            width: 150px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: $mob) {
  .offset-table {
    &__title {
      margin-bottom: 16px;
    }

    &__table {}

    &__specification {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 42px;

      border-radius: 55px;
      background-color: $white;
      box-shadow: 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
      white-space: nowrap;
      width: 100%;
      cursor: pointer;
      margin-top: 24px;

      span {
        font-size: 14px;
        font-weight: 700;
        line-height: 20px;
        letter-spacing: 0.7px;
        text-transform: uppercase;
        background: var(--Linear, linear-gradient(90deg, #004D43 1.97%, #02B45E 96.94%));
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }

  table {
    thead {
      tr {

        th,
        td {
          font-size: 12px;
          line-height: 16px;
          padding: 8px 12px;
        }
      }
    }
  }

  tbody {
    tr {
      th {
        padding: 8px 12px;
        font-size: 12px;
        line-height: 16px;
        width: 140px;
      }

      td {
        padding: 8px 12px;
        font-size: 12px;
        line-height: 16px;

        &:nth-child(2) {
          width: 92px;
        }
      }
    }
  }
}

